@import '../../Public/tools.scss';

.c-modal {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5
}
.c-modal__bg {
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,0.2);
  top: 0;
  left: 0;
  cursor: pointer;
  z-index: 3;
}
.c-modal__modal {
  width: 500px;
  margin: 0 $spacing_100;
  background: #FFFFFF;
  padding: $spacing_100;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.06);
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.06);
  border-radius: calc(#{$border-radius} * 2);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  z-index: 4;
  position: absolute;

}
.c-modal__header {
  width: calc(100% + #{$spacing_200});
  color: #484848;
  margin-left: -$spacing_100;
  margin-top: -$spacing_100;
  margin-bottom: $spacing_100;
  padding: $spacing_100;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 14px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

@media (max-width: 533px) {
  .c-modal__modal {
    width: calc(100% - #{$spacing_200});
  }
}



